.movieInfo {
  background: rgba(0, 0, 0, 0.6);
  text-align: left;
  max-width: 960px;
  margin: 5rem auto 0;
  display: -ms-flexbox;
  display: flex;
}

.details {
  padding: 10px 20px 20px;
  color: #fff;
}

.poster {
  width: 240px
}

.title {
  font-size: 2.4em;
  font-weight: bold;
  text-transform: uppercase;
}
.additional_details,
.tagline {
  font-family: Oswald, sans-serif;
}
.additional_details {
  margin-top: 20px;
  display: grid;
  grid-gap: 10px 4px;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
}
.meta-data {
  display: block;
  color: #00fc87;
  font-size: 1.6em;
  line-height: 1.1em;
}



