body {
  background-color: #252627;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
body:before {
  content: "";
  position: fixed;
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    color-stop(15%, rgba(0, 0, 0, 0.75)),
    color-stop(40%, rgba(0, 0, 0, 0.2)),
    color-stop(90%, rgba(0, 0, 0, 0.75))
  );
  background: -webkit-linear-gradient(
    rgba(0, 0, 0, 0.75) 15%,
    rgba(0, 0, 0, 0.2) 40%,
    rgba(0, 0, 0, 0.75) 90%
  );
  background: -o-linear-gradient(
    rgba(0, 0, 0, 0.75) 15%,
    rgba(0, 0, 0, 0.2) 40%,
    rgba(0, 0, 0, 0.75) 90%
  );
  background: linear-gradient(
    rgba(0, 0, 0, 0.75) 15%,
    rgba(0, 0, 0, 0.2) 40%,
    rgba(0, 0, 0, 0.75) 90%
  );
  width: 100%;
  height: 100%;
  z-index: -1;
}

.App-header {
  background-color: #111;
  height: 88px;
  padding: 20px 4%;
  color: white;
}




